.react-tabs {
	-webkit-tap-highlight-color: transparent;
  }
  
  .react-tabs__tab-list {
	border-bottom: 1px solid black;
	margin: 0 0 0 0;
	color: rgb(0, 0, 0);
  }
  
  .react-tabs__tab {
	display: inline-block;
	border: 1px solid transparent;
	border-bottom: none;
	bottom: -1px;
	position: relative;
	list-style: none;
	padding: 6px 12px;
	cursor: pointer;
  }
  
  .react-tabs__tab--selected {
	background: #fff;
	border-color:black;
	color: black;
	border-radius: 5px 5px 0 0;
  font-weight:bold;
  border-bottom:1px solid transparent;
  }
  
  .react-tabs__tab--disabled {
	color: GrayText;
  cursor: default;
  }
  
  
  .react-tabs__tab:focus:after {
	content: "";
	position: absolute;
	height: 5px;
	left: -4px;
	right: -4px;
	bottom: -5px;
	background: #fff;
  }
  
  .react-tabs__tab-panel {
	display: none;
  }
  
  .react-tabs__tab-panel--selected {
	display: block;
  }

  


